import { Injectable } from '@angular/core';
import { gettext, NavigatorNode, NavigatorNodeFactory } from '@c8y/ngx-components';

@Injectable()
export class NavigationFactory implements NavigatorNodeFactory {
  navs: NavigatorNode[] = [];

  async get() {
    if (this.navs.length === 0) {
      this.navs.push(
        new NavigatorNode({
          label: gettext('Device control'),
          path: 'devicecontrol/single',
          icon: 'c8y-icon c8y-icon-device-control',
          parent: gettext('Overviews'),
          priority: 1360
        })
      );
    }
    return this.navs;
  }
}
